/* Common styles for both forms */
.formContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* grid-template-rows: auto auto auto; */
    gap: 20px;
    padding: 30px;
    position: fixed;
    left: 55%;
    top: 45%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    background-color: rgba(245, 245, 245, 0.95);
    width: 90%;
    max-width: 1200px;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

@media (max-width: 768px) {
    .formContainer {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 90%;
        max-width: 400px;
        z-index: 1000;
        background: white;
        padding: 20px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
}

/* Room and date section */
.guestInfoContainer {
    position: relative;
}

.guestFields {
    position: absolute;
    left: calc(100% + 10px);
    top: 0;
    width: 200px;
    background-color: #f0f0f0;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    z-index: 1001;
}

.guestFields .MuiFormControl-root {
    margin-bottom: 10px;
}

.guestFields .MuiInputBase-root {
    font-size: 0.9em;
}

.guestToggleButton {
    position: absolute;
    right: -30px;
    top: 0;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.2em;
}

.guestInfoDropdown {
    position: absolute;
    z-index: 1000;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    width: 300px;
    max-height: 300px;
    /* Ajusta esta altura según tus necesidades */
    overflow-y: auto;
    /* Esto añade el scroll vertical */
}

.guestInfoDropdown .MuiTextField-root {
    margin-bottom: 10px;
}

.guestInfoDropdown TextField {
    margin-bottom: 10px;
}

.guestInfoDropdown .MuiButton-root {
    display: block;
    margin-left: auto;
}

.paymentMethodContainer,
.roomTypeContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* flex-direction: column; */
    width: 100%;
}

.paymentMethodContainer label,
.roomTypeContainer label {
    margin-bottom: 5px;
}

.paymentMethodContainer select,
.roomTypeContainer select {
    /* width: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 10px; */
}

/* Hacer que el tipo de habitación ocupe un espacio 2x2 */
/* .roomTypeContainer select {
    grid-column: 2;
    grid-row: 1;
} */
.triplecuadruple {
    grid-row: 2;
}

.paymentMethodContainer {
    grid-column: 2;
    grid-row: 2;
}

/* Asegurarse de que los elementos del formulario se ajusten al grid */
.formContainer>* {
    min-width: 0;
}